import React, {useEffect, useState} from "react";

// import { Model } from "./models";
import {
  Box,
  Button,
  Card,
  CircularProgress,
  Container,
  MenuItem,
  Select,
  SelectChangeEvent,
  TextField,
  Typography
} from '@mui/material';
import {CopyAllRounded, PsychologyOutlined} from '@mui/icons-material'; // import the Model interface from a separate file

const sleep = (ms: number) => new Promise(r => setTimeout(r, ms));

class Model {
  id: string;
  name: string;
  preText: string;
  postText?: string;
  separator?: string;

  constructor(id: string,
              name: string,
              preText: string,
              postText?: string) {
    this.id = id;
    this.name = name;
    this.preText = preText;
    this.postText = postText;
    this.separator = '\n';

  }

  preparePrompt(input: string): string {
    return this.preText +
      this.separator +
      input +
      this.separator +
      ( this.postText ? this.postText : '');
  }

  async process(input: string): Promise<string> {
    await sleep(1000);


    return  Promise.resolve('The completion comming from the server');
  }


}

const avaliableModels: Model[] = [
  new Model("0", "✨ - A clean prompt", ""),
  new Model("1", "Traduction facile", "Translate the following text to francais"),
  new Model("2", "Cocktail de textes", "Basé dans les extraits suivants, redige un texte contenant", "Réformulation: "),
  new Model("3", "Appel Voltaire", "Le texte suivant peut etre reecrit avec un style soigné et elegant avec une grammaire parfaite: ", "Réformulation: "),
  new Model("4", "Resume ", "Le résumé du text suivant: ", 'est:'),
  new Model("5", "De quoi ca parle", "Le texte suivant: ", 'parle de'),
  new Model("6", "Un mail pour dire non", "Message: ", "Une réponse pour dire non: "),
  new Model("7", "Un mail pour dire oui", "Message: ", "Une réponse pour continuer "),
  new Model("8", "Mot clés", "Ecrit un texte basé sur les mot clé suivants"),
];

const Modeler: React.FC = () => {

  const [models, setModels] = useState<Model[]>(avaliableModels); // initialize an empty array of models
  const [selectedModel, setSelectedModel] = useState<Model | null>(models[0]); // initialize the selected model as null
  const [inputText, setInputText] = useState<string>(""); // initialize the input text as an empty string
  const [completion, setCompletion] = useState<string>(""); // initialize the completion as an empty string
  const [isLoading, setIsLoading] = useState<boolean>(false); // initialize the loading state as false
  const [prompt, setPrompt] = useState<string>(""); // initialize the prompt as an empty string
  const [numberTokensToProcess, setNumberTokensToProcess] = useState<number>(0); // initialize the tokens to process as an empty array

  const handleModelChange = (
    event: SelectChangeEvent
  ) => {
    const modelId = event.target.value as string;
    const model = models.find((m) => m.id === modelId);
    setSelectedModel(model || null);
  };

  const handleInputChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    // update the input text based on user input
    setInputText(event.target.value);
  };

  const handleUseModelClick = async  () => {
    // use the selected model to process the input text
    if (selectedModel) {
      setIsLoading(true);
      try {
        const result = await selectedModel.process(inputText);
        setCompletion(result);
        setIsLoading(false)
      } catch (e) {
        console.error(e);
        setIsLoading(false);
      }
    }
  };

  useEffect(() => {
    setPrompt(
      selectedModel?.preparePrompt(inputText) || ""
    );
    setNumberTokensToProcess(prompt.split(' ').length || 0);

  }, [selectedModel, inputText] );


  return (

    <Container>
      <Box display='grid' flexDirection="column" alignItems="center">
        <Typography variant="h6" sx={{fontWeight: 'light'}}>Model:</Typography>
        <Select value={selectedModel?.id || ""} onChange={handleModelChange}>
          {
            models.map((model) => (
              <MenuItem key={model.id} value={model.id}>
                {model.name}
              </MenuItem>
            ))}
        </Select>
      </Box>

      {
        selectedModel && selectedModel.preText &&
        <div>
          <Typography sx={{fontStyle: 'italic'}}>{selectedModel.preText}</Typography>
        </div>
      }
      <Box display='grid' flexDirection="column" alignItems="center">

        <TextField
          value={inputText}
          multiline
          rows={8}
          variant="filled"
          placeholder={'Ici votre prompt !'}

          onChange={handleInputChange}/>
      </Box>

      {selectedModel && selectedModel.postText &&
        <Typography>{ selectedModel.postText }</Typography>
      }

      {isLoading &&
        <div>
          <Box display="flex" flexDirection="column" alignItems="center">
            <CircularProgress/>
          </Box>

        </div>
      }

      <Box display="flex" flexDirection="column" alignItems="center">
        <Button variant="contained" color="primary" onClick={handleUseModelClick}>
          <PsychologyOutlined/>
          {numberTokensToProcess} ~ tokens
        </Button>
      </Box>


      {
        completion &&
        <div>
          <Card>
            <Typography>{completion}</Typography>

            <Box display="flex" flexDirection="column" alignItems="center">


            </Box>

          </Card>

          <Box display="flex" flexDirection="column" alignItems="center">

            <Button
              variant="contained"
              color="primary"
              onClick={() => {
                navigator.clipboard.writeText(completion)
              }}
            >
              <CopyAllRounded/>
            </Button>
          </Box>
        </div>
      }


    </Container>
  );
};

export default Modeler;

