import React, {useContext} from "react";
import {Button} from '@mui/material';
import {ItemsContext} from "./ItemsContext";

interface MoonProps {
  phase: "new" | "waxing-crescent" | "first-quarter" | "waxing-gibbous" | "full" | "waning-gibbous" | "last-quarter" | "waning-crescent";
}

const emojiDictionary: Record<MoonProps["phase"], string> = {
  "new": "🌑",
  "waxing-crescent": "🌒",
  "first-quarter": "🌓",
  "waxing-gibbous": "🌔",
  "full": "🌕",
  "waning-gibbous": "🌖",
  "last-quarter": "🌗",
  "waning-crescent": "🌘",
};


function getCurrentMoonPhase(): MoonProps["phase"] {
  const now = new Date();
  const year = now.getFullYear();
  const month = now.getMonth() + 1;
  const day = now.getDate();

  const c = Math.floor((year / 100) + 1);
  const e = Math.floor((c * 3) / 4);
  const jd = Math.floor(365.25 * (year + 4716)) + Math.floor(30.6001 * (month + 1)) + day - 1524.5;
  const b = ((jd - 2451549.5) - e) / 29.530588853;
  const phase = Math.floor((b - Math.floor(b)) * 8);

  switch (phase) {
    case 0:
      return "new";
    case 1:
      return "waxing-crescent";
    case 2:
      return "first-quarter";
    case 3:
      return "waxing-gibbous";
    case 4:
      return "full";
    case 5:
      return "waning-gibbous";
    case 6:
      return "last-quarter";
    case 7:
      return "waning-crescent";
    default:
      return "new";
  }
}

const Moon = () => {
  const phaseName = getCurrentMoonPhase();
  const emoji = emojiDictionary[phaseName];
  const {items, saveRepository} = useContext(ItemsContext);

  const onMoonClick = () => {
    // alert("The moon is currently in the " + items.length + " phase.");
    console.log(items);
    saveRepository(items).then((success: any) => {
      if (success) {
        alert("Saved!");
      } else {
        alert("Failed to save");
      }
    })
  }

  return (
    <div>
      <Button onClick={onMoonClick}>{emoji}</Button>
    </div>
  );
};

export default Moon;
