import React, {useContext, useState} from 'react';
import {useNavigate} from 'react-router-dom';
import {AuthContext} from "../../infrastructure/ui/AuthContext";
import {Box, Button} from "@mui/material";
import {useMsal} from "@azure/msal-react";
import {loginRequest} from '../../infrastructure/ui/authConfig';
import {SmartToy} from '@mui/icons-material';

export const SignInButton = ({setUsername}: { setUsername: any }) => {
  const {instance} = useMsal();

  const handleLogin = async (loginType: string) => {
    if (loginType === "popup") {
      try {
        const authResult = await instance.loginPopup(loginRequest);


        console.log(authResult.account?.name);
        if (authResult.account?.name) {
          setUsername(authResult.account?.name);

        }
      } catch (e) {
        console.log(e);
      }
    }
  }

  return (
    <Button className="ml-auto" onClick={() => handleLogin("popup")}>Sign in using Popup</Button>
  );
}


const Login = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState('');
  const {login} = useContext(AuthContext);
  const navigate = useNavigate();

  const handleLogin = async (event: React.FormEvent) => {
    event.preventDefault();

    try {
      await login(username, password);
      navigate('/');
    } catch (error) {
      setError('Invalid username or password');
    }
  };

  return (
    <div>

      <Box className="container">
        <Box className="top"/>
        <Box className="bottom"/>
        <Box className="center">
          <Button onClick={handleLogin}>
            <SmartToy/>
          </Button>


        </Box>
      </Box>
    </div>
  )
};
export default Login;

